.toggle-switch {
  position: relative;
  margin-bottom: 10px;
  width: 90px;
  height: 25px;
  --light: #d8dbe0;
  --dark: #28292c;
  --link: rgb(27, 129, 112);
  --link-hover: rgb(24, 94, 82);
}
  
.switch-label {
  position: absolute;
  width: 80%;
  height: 35px;
  background-color: var(--dark);
  border-radius: 25px;
  cursor: pointer;
  border: 3px solid var(--dark);
}
  
.checkbox {
  position: absolute;
  display: none;
}
  
.slider {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 25px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
  
.checkbox:checked ~ .slider {
  background-color: var(--light);
}
  
.slider::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  -webkit-box-shadow: inset 12px -4px 0px 0px var(--light);
  box-shadow: inset 12px -4px 0px 0px var(--light);
  background-color: var(--dark);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
  
.checkbox:checked ~ .slider::before {
  -webkit-transform: translateX(38px);
  -ms-transform: translateX(38px);
  transform: translateX(38px);
  background-color: var(--dark);
  -webkit-box-shadow: none;
  box-shadow: none;
}

